@font-face {
  font-family: "Geometria";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/geometriamedium.woff2") format("woff2"), url("../fonts/geometriamedium.woff") format("woff"); }

@font-face {
  font-family: "Geometria";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/geometriabold.woff2") format("woff2"), url("../fonts/geometriabold.woff") format("woff"); }

body {
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  font-family: "Geometria", "Arial", sans-serif;
  font-size: 12px;
  font-style: normal;
  line-height: 20px;
  color: #8e80a9;
  min-width: 320px;
  max-width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  position: relative; }

img {
  max-width: 100%;
  height: auto; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

a {
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden,
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }
